export default {
	data() {
		return {
			addressName:{}
		};
	},
	methods: {
		getAuthorizeInfo(a = 'scope.userLocation') {
			//1. uniapp弹窗弹出获取授权（地理，个人微信信息等授权信息）弹窗
			var _this = this;
			uni.authorize({
				scope: a,
				success() {
					//1.1 允许授权
					_this.getLocationInfo();
				},
				fail() {
					//1.2 拒绝授权
					uni.showModal({
					    title: '提示',
					    content: '你拒绝了授权，无法获得周边信息',
					    success: function (res) {
					        if (res.confirm) {
					            console.log('用户点击确定');
					        } else if (res.cancel) {
					            console.log('用户点击取消');
					        }
					    }
					});
				}
			});
		},
		getLocationInfo() {
			//2. 获取地理位置
			var _this = this;
			uni.getLocation({
				type: 'wgs84',
				success(res) {
					console.log('你当前经纬度是：');
					console.log(res);
					let latitude, longitude;
					latitude = res.latitude.toString();
					longitude = res.longitude.toString();
					uni.request({
						header: {
							'Content-Type': 'application/text'
						},
						url: 'http://apis.map.qq.com/ws/geocoder/v1/?location=' + latitude + ',' + longitude + '&key=MVGBZ-R2U3U-W5CVY-2PQID-AT4VZ-PDF35',
						success(re) {
							console.log('中文位置');
							console.log(re);
							_this.addressName=re.data.result.address
							console.log(_this.addressName)
							if (re.statusCode === 200) {
								console.log('获取中文街道地理位置成功');
							} else {
								uni.showModal({
								    title: '提示',
								    content: '获取信息失败，请重试！',
								    success: function (res) {
								        if (res.confirm) {
								            console.log('用户点击确定');
								        } else if (res.cancel) {
								            console.log('用户点击取消');
								        }
								    }
								});
							}
						}
					});
				}
			});
		},
		isGetLocation(a = 'scope.userLocation') {
			// 3. 检查当前是否已经授权访问scope属性，参考下截图
			var _this = this;
			uni.getSetting({
				success(res) {
					if (!res.authSetting[a]) {
						//3.1 每次进入程序判断当前是否获得授权，如果没有就去获得授权，如果获得授权，就直接获取当前地理位置
						_this.getAuthorizeInfo();
					} else {
						_this.getLocationInfo();
					}
				}
			});
		},
		selcetAddress(){
			var _this = this;
			uni.chooseLocation({
			    success: function (res) {
					_this.addressName=res.address
			    }
			});
		}
	},
	mounted() {
		this.isGetLocation();
	}
};
